let that = {}
import dataCn from "./dataCn.js"
//隐藏hide的作品
for (let i = 0; i < dataCn.products.list.length; i++) {
    if (typeof dataCn.products.list[i].hide != "undefined" && dataCn.products.list[i].hide == 1) {
        dataCn.products.list.splice(i, 1)
    }
}
const jsonDataCn = dataCn
import { Loading } from 'element-ui';

// 加载动画
let loadingInstance = Loading.service({ fullscreen: true });
export default {
    name: "Portfolio",
    data() {
        return {
            pageData: JSON.parse(JSON.stringify(jsonDataCn)),
            currentCateID: 0,
            model: {
                //总页数
                total: 10,
                //每页显示条目个数不传默认10
                size: 4,
                //当前页码
                page: 1,
            },
            //是否在框架内
            ifInIframe: true,
            saveProductList: [],
            currentSelectPage: 1,
            currentSelectCate: 0,
            // 二维码 pc端
            qrCodeDialog: false,
            // 二维码显示 移动端
            centerDialogVisible: false,
            detailsDialogVisible: false,
            productItem: {},
            productList: [],
            device: 'pc',
        }
    },
    created() {
        that = this
        setTimeout(function () {
            that.$nextTick(() => { // 以服务的方式调用的 Loading 需要异步关闭
                loadingInstance.close();
            });
        }, 1850)

        this.ifInIframe = self.frameElement && self.frameElement.tagName == "IFRAME";
        /** 初始化页码 */
        this.model.total = that.pageData.products.list.length;

        /** 初始化分类count */
        let categoryList = that.pageData.products.categoryList;
        let productList = that.pageData.products.list;
        for (let i = 0; i < productList.length; i++) {
            categoryList[0].count = (categoryList[0].count ? categoryList[0].count : 0) + 1;
        }
        for (let k = 1; k < categoryList.length; k++) {
            categoryList[k].count = categoryList[k].prodID.length
        }
        that.pageData.products.categoryList = categoryList

        // 检测是否移动端设备
        let w = document.documentElement.offsetWidth || document.body.offsetWidth;
        if (/AppleWebKit.*Mobile/i.test(navigator.userAgent) || (/MIDP|SymbianOS|NOKIA|SAMSUNG|LG|NEC|TCL|Alcatel|BIRD|DBTEL|Dopod|PHILIPS|HAIER|LENOVO|MOT-|Nokia|SonyEricsson|SIE-|Amoi|ZTE/.test(navigator.userAgent))) {
            try {
                if (/Android|webOS|iPhone|iPod|iPad|BlackBerry/i.test(navigator.userAgent)) {
                    if (w >= 1020) {
                        that.device = 'pc';
                    } else {
                        that.device = 'm';
                    }
                } else if (/iPad/i.test(navigator.userAgent)) {
                    //ipad
                } else {
                    //other
                }
            } catch (e) {
                console.log(e);
            }
        }
        that.productListData()
        // 进行作品图片预加载处理
        setTimeout(function () {
            that.preloadimages(that.pageData.products.list)
        }, 1000 * 4.5)

        // 处理作品页-标题 更新默认 
        setTimeout(function () {
            let title = document.title
            let titleArr = title.split("|")
            document.title = "作品 |" + titleArr[1]
        }, 1000)
    },
    mounted() {
        let pageVal = sessionStorage.getItem("currentPage")
        pageVal = parseInt(pageVal)
        if (pageVal > 1) {
            that.pageFn(pageVal)
        }
    },
    methods: {
        changeCategoryProduct(selectValue) {
            that.$set(that.pageData.products, 'list', [])
            let currentCategory = {}//当前选中分类
            // 设置选中效果
            for (let cateItem in jsonDataCn.products.categoryList) {
                if (jsonDataCn.products.categoryList[cateItem].name == selectValue) {
                    that.currentCateID = jsonDataCn.products.categoryList[cateItem].id;
                    currentCategory = jsonDataCn.products.categoryList[cateItem]
                }
            }
            if (that.currentCateID == 0) {//选中全部
                for (let i = 0; i < jsonDataCn.products.list.length; i++) {
                    that.pageData.products.list.push(jsonDataCn.products.list[i]);
                }
            } else {//选中某个分类
                for (let j = 0; j < currentCategory.prodID.length; j++) {
                    for (let i = 0; i < jsonDataCn.products.list.length; i++) {
                        if (currentCategory.prodID[j] == jsonDataCn.products.list[i].id) {
                            that.pageData.products.list.push(jsonDataCn.products.list[i]);
                        }
                    }
                }
            }
            that.productListData()
            // 重置返回第一页
            document.getElementsByClassName('el-pager')[0].getElementsByTagName('li')[0].click();
        },
        changeToEnglish() {
            this.pageData = jsonDataEn;
            that.pageData = jsonDataEn;
            // document.title = this.pageData.title;
        },
        changeToCn() {
            this.pageData = JSON.parse(JSON.stringify(jsonDataCn));
            // document.title = this.pageData.title;
        },
        // 列表数据预处理
        productListData: function () {
            // 刷新底部分页按钮
            let tempData = that.pageData.products.list
            that.model.total = tempData.length;
            that.productList = tempData.slice(that.model.size * (that.model.page - 1), (that.model.size * (that.model.page - 1)) + that.model.size)
        },

        //页码切换执行方法
        pageFn(val) {
            this.model.page = val;
            document.body.scrollTop = 0;
            document.documentElement.scrollTop = 0;

            // 处理页码问题
            sessionStorage.setItem("currentPage", val)
            that.productListData()
        },
        openDetails(item) {
            alert(item.name);
            this.$alert('这是一段内容', item.name, {
                confirmButtonText: '关闭',
                callback: action => {
                    this.$message({
                        type: 'info',
                        center: true,
                        message: `action: ${action}`
                    });
                }
            });
        },
        // PC预览图片
        showBigImg(e) {
            let nextImgShow = e.target.parentElement.querySelectorAll('.el-image-viewer__actions .el-icon-zoom-out');
        },
        // 预览图片界面
        go2PreImgPage(item, e) {
            localStorage.setItem('prevImgItem', JSON.stringify(item));
            window.open('src/view/preview.html');
        },
        // 显示二维码
        showQrCode(productList, index) {
            this.productItem = productList;
            this.centerDialogVisible = true;
            this.qrCodeDialog = true;
        },
        // 关闭手机端二维码展示
        mobileQRDilogClose() {

        },
        // 跳转到详情页面 portfolio/
        go2Link(link) {
            parent.window.location.href = ('../../' + link);
        },
        // 图片预加载
        preloadimages(arr) {
            var newimages = []
            var arr = (typeof arr != "object") ? [arr] : arr //确保参数总是数组
            for (var i = 0; i < arr.length; i++) {
                newimages[i] = new Image()
                newimages[i].src = arr[i].img
            }
        },
        // 图片加载成功回调方法
        imgLoadSuccess() {
            setTimeout(function () {
                that.$nextTick(() => { // 以服务的方式调用的 Loading 需要异步关闭
                    loadingInstance.close();
                });
            }, 50)
        },
        // /methods
    },
};


IEVersion();

function IEVersion() {
    let userAgent = navigator.userAgent;
    //取得浏览器的userAgent字符串
    let isIE = userAgent.indexOf("compatible") > -1 && userAgent.indexOf("MSIE") > -1;
    //判断是否IE11浏览器
    let isEdge = userAgent.indexOf("Edge") > -1 && !isIE;
    //判断是否IE的Edge浏览器
    let isIE11 = userAgent.indexOf('Trident') > -1 && userAgent.indexOf("rv:11.0") > -1;
    if (isIE || isIE11) {
        location.href("./res/view/error/error.html");
        let reIE = new RegExp("MSIE (\\d+\\.\\d+);");
        reIE.test(userAgent);
        let fIEVersion = parseFloat(RegExp["$1"]);
        if (fIEVersion < 9) {
            location.href("./res/view/error/error.html");
        }
    } else if (isEdge) {
        return 'edge';
        //edge
    } else {
        return -1;
        //不是ie浏览器
    }
}

window.onresize = function () {
    window.location.reload()
}



